.node-start {
  border-radius: 50%;
  animation-name: growAnimation;
  animation-duration: 0.6s;
  animation-timing-function: ease-out;
  animation-delay: 0;
  animation-direction: alternate;
  animation-iteration-count: 1;
  animation-fill-mode: forwards;
  animation-play-state: running;
}
.node-end {
  border-radius: 50%;
  animation-name: growAnimation;
  animation-duration: 0.6s;
  animation-timing-function: ease-out;
  animation-delay: 0;
  animation-direction: alternate;
  animation-iteration-count: 1;
  animation-fill-mode: forwards;
  animation-play-state: running;
}
.node-wall {
  background-color: rgb(12, 53, 71);
  animation-name: growAnimation;
  animation-duration: 0.3s;
  animation-timing-function: ease-out;
  animation-delay: 0;
  animation-direction: alternate;
  animation-iteration-count: 1;
  animation-fill-mode: forwards;
  animation-play-state: running;
}

@keyframes growAnimation {
  0% {
    transform: scale(.3);
  }
  50% {
    transform: scale(1.2);
  }
  100% {
    transform: scale(1.0);
  }
}
.node-revisited {
  color: white;
  animation-name: reVisitedAnimation;
  animation-duration: 1.5s;
  animation-timing-function: ease-out;
  animation-delay: 0;
  animation-direction: alternate;
  animation-iteration-count: 1;
  animation-fill-mode: forwards;
  animation-play-state: running;
}
@keyframes reVisitedAnimation {
  0% {
    transform: scale(0.3);
    background-color: rgba(0, 0, 66, 0.75);
    border-radius: 100%;
  }
  
  50% {
    background-color: rgba(73, 34, 179, 0.75);
  }

  75% {
    transform: scale(1.2);
    background-color: rgba(110, 12, 134, 0.75);
  }
  
  100% {
    transform: scale(1);
    background-color: rgba(211, 18, 250, 0.75);
  }
}
.node-visited {
  color: white;
  animation-name: visitedAnimation;
  animation-duration: 1.5s;
  animation-timing-function: ease-out;
  animation-delay: 0;
  animation-direction: alternate;
  animation-iteration-count: 1;
  animation-fill-mode: forwards;
  animation-play-state: running;
}
@keyframes visitedAnimation {
  0% {
    transform: scale(0.3);
    background-color: rgba(0, 0, 66, 0.75);
    border-radius: 100%;
  }
  
  50% {
    background-color: rgba(17, 104, 217, 0.75);
  }

  75% {
    transform: scale(1.2);
    background-color: rgba(0, 217, 159, 0.75);
  }
  
  100% {
    transform: scale(1);
    background-color: rgba(0, 190, 218, 0.75);
  }
}
.node-solution {
  color: white;
  background-color: #28ff70;
  animation-name: solutionAnimation;
  animation-duration: 0.3s;
  animation-timing-function: ease-out;
  animation-delay: 0;
  animation-direction: alternate;
  animation-iteration-count: 1;
  animation-fill-mode: forwards;
  animation-play-state: running;
}
@keyframes solutionAnimation {
  0% {
    transform: scale(.3);
  }
  50% {
    transform: scale(1.2);      
  }
  100% {
    transform: scale(1.0);      
  }
}